































import Vue from 'vue'

export default Vue.extend({
  mounted: function () {
    this.$store.dispatch("userModule/init");
    this.$store.dispatch("editShopingListModule/init");
  },
  data: function() {
    return {
      menuOpen: false  
    }
  },
  methods: {
     mobileMenu: function () {
          if (this.menuOpen == false) {
            this.menuOpen = true;
          } else {
            this.menuOpen = false;
          }
     }
  }
})
